.fg {
  position: relative;
  margin: 0;
  input,
  select,
  textarea{
    border: 1px solid $lightGrayBg;
    padding: 0 1em;
    height: 3.13em;
    width: 100%;
    font-size: .9375em;
    line-height: 140%;
    background-color: $lightGrayBg;
    border-radius: .33em;
    color: $textBlack;
    &::placeholder {
      color: $textBlack;
    }
  }
  select{
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    background-image: url(../img/arrow-bottom.svg);
    background-repeat: no-repeat;
    background-position: right .75em center;
    background-size: 1.33em auto;
    &::-ms-expand {
      display: none; // IE
    }
  }
  textarea{
    height: 9.5em;
    resize: none;
    padding-top: 1em;
    padding-bottom: 1em;
  }
}

.m-check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
        background-image: url(../img/check4.png);
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 1.8125em;
    position: relative;
    min-height: 1.1875em;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      background-color: #ffffff;
      border: 1px solid #e1e1e1;
      border-radius: .3125em;
      width: 1.1875em;
      height: 1.1875em;
      left: 0;
      top: 0;
      background-size: .625em auto;
      background-position: center;
      background-repeat: no-repeat;
    }
    span{
      font-size: 1em;
      line-height: 118.75%;
    }
  }
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field{
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: 1.125em;
    height: 1.125em;
    margin: 0 .3125em 0 0;
    svg{
      max-width: 100%;
      max-height: 100%;
      fill: #c3c3c3;
    }
    &:hover {
      cursor: pointer;
      svg{
        fill: $primary;
      }
      &~.rating-input__item {
        svg{
          fill: $primary;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
  svg{
    fill: $primary;
  }
}

.file-input{
  display: flex;
  align-items: center;
  position: relative;
  input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    cursor: pointer;
    opacity: 0;
  }
  svg{
    width: 1.25em;
    height: 1.25em;
    margin-right: .625em;
    fill: $primary;
  }
  span{
    font-size: 1em;
    line-height: 120%;
    letter-spacing: 0.025em;
  }
}


@media screen and (max-width: 992px){
  .rating-input{
    &__item{
      width: .875em;
      height: .875em;
      margin-right: .125em;
    }
  }
}
