.services-1c{
  margin-top: 3em;
  &--mt0{
    margin-top: 0;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
    gap: .875em 1.25em;
    margin-top: 1.25em;
  }
  &__link{
    width: 100%;
    height: 5em;
    border-radius: .5em;
    text-align: center;
    span{
      font-size: 1.0625em;
      line-height: 142%;
      letter-spacing: 0.025em;
    }
    &._active{
      background-color: $primary;
      color: #fff;
      pointer-events: none;
    }
  }
}

.services-page,
.work-detail{
  &__inner{
    position: relative;
  }
  &__rects{
    position: absolute;
    z-index: -1;
    bottom: 1.5em;
    left: -3.0625em;
    width: 92.625em;
  }
}

.services-page{
  padding: 0 0 2.375em;
  .direction-consult{
    margin-bottom: 0;
  }
}

.work-detail{
  padding: 2.75em 0;
  background-color: $lightGrayBg;
  &__rects{
    z-index: unset;
  }
}

.work-table{
  margin-top: 1em;
  table{
    width: 100%;
    border-spacing: 0 .5em;
  }
  tbody{
    tr{
      transition: all .3s;
      &:hover{
        box-shadow: .125em .25em 2.1875em 0 rgba(90,91,91,0.18);
      }
    }
  }
  td,
  th{
    padding-left: 1.25em;
    text-align: left;
  }
  td{
    overflow: hidden;
    background-color: #fff;
    height: 3.25em;
    letter-spacing: 0.025em;
    &:first-child{
      border-radius: .5em 0 0 .5em;
      width: 22em;
    }
    &:last-child{
      border-radius: 0 .5em .5em 0;
      width: 21em;
    }
  }
  th{
    span{
      font-size: 1.125em;
      line-height: 120%;
      font-weight: 700;
    }
  }
  &__action{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn{
    width: 6.25em;
    height: 100%;
    border-radius: 0 .5em .5em 0;
  }
  &__title{
    display: none;
  }
  .text{
    padding: .5em 0;
    ol{
      &:first-child{
        margin-top: 0;
      }
    }
  }
  @media screen and (min-width: 993px){
    &._type2{
      td{
        &:last-child{
          width: 16.25em;
        }
      }
      .work-table{
        &__action{
          justify-content: flex-end;
        }
        &__price{
          margin-right: 1.25em;
          text-align: right;
        }
        &__btn{
          width: 7em;
        }
      }
    }
  }
}

.equip2._white-bg{
  background-color: #fff;
}



@media screen and (max-width: 992px){
  .services-page{
    padding-bottom: 1.5em;
    &__rects{
      display: none;
    }
  }

  .services-1c{
    margin-top: 2em;
    &__items{
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
    &__link{
      height: 3.5em;
    }
  }

  .work-detail{
    padding: 1.5em 0;
    &__rects{
      display: none;
    }
  }

  .work-table{
    table{
      display: block;
    }
    thead{
      display: none;
    }
    tbody{
      @include grid100gap(1.5em);
    }
    tr{
      @include grid100gap(1em);
      background-color: #fff;
      border-radius: .5em;
      padding: 1.25em;
    }
    td{
      padding: 0;
      background-color: transparent;
      width: 100% !important;
      height: unset;
      display: block;
    }
    &__title{
      display: block;
      margin-bottom: .5em;
      color: $textGray;
      font-weight: 700;
      &:after{
        content: ':';
      }
    }
    &__action{
      @include grid100gap(1em);
    }
    &__btn{
      height: 3.5em;
      width: 100%;
      border-radius: .5em;
    }
  }
}


@media screen and (max-width: 576px){
  .services-1c{
    &__items{
      grid-template-columns: 100%;
    }
  }
}
